* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --background: #0e0e0e;
  --text-purple: #dc56c5;
  --text-gray: #ccc;
  --white: #fff;
  --modal-blue: #4c00fe;
}

.title-container h1 {
  color: var(--text-purple);
  text-align: center;
  font-family: Montserrat, serif;
  font-size: 18px;
  font-weight: 900;
  line-height: 21.94px;
}

body {
  background-color: var(--background);
  font-family: Montserrat, serif;
}

header {
  background: url("pic-mobile.0a71861a.png") center / cover no-repeat;
  width: 100%;
  height: 600px;
}

.header-container {
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  display: flex;
}

.header-content {
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.logo-container {
  backdrop-filter: blur(5px);
  background: #363636cc;
  border-radius: 0% 100% 100% 0% / 0% 0% 100% 100%;
  width: 106px;
  height: 92px;
  transition: transform .5s ease-in-out, width .5s ease-in-out, height .5s ease-in-out;
  transform: scale(1);
}

.logo {
  background-image: url("logo.d3b7ba40.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 72px;
  margin-top: 16px;
  margin-left: 8px;
  transition: transform .5s ease-in-out, margin .5s ease-in-out;
}

.title-container {
  flex-direction: column;
  align-items: center;
  width: 70%;
  margin-top: 18px;
  display: flex;
}

.header-form {
  flex-direction: column;
  gap: 40px;
  margin-top: 20px;
  margin-bottom: 109px;
  display: flex;
}

.header-form input {
  backdrop-filter: blur(5px);
  background: #fff3;
  border: none;
  border-radius: 20px;
  width: 280px;
  height: 40px;
}

input[type="text"], input::placeholder {
  color: var(--text-gray);
  padding-left: 25px;
  font-size: 14px;
  font-weight: 400;
  line-height: 19.5px;
}

.search-icon {
  background-image: url("search.47f8aa53.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 19px;
  height: 21px;
  position: absolute;
  top: 13px;
  right: 13px;
}

.input-container {
  position: relative;
}

#loader {
  z-index: 6;
  background: linear-gradient(#0000 -1%, #413c3c 3%);
  width: 100%;
  height: 100%;
  position: fixed;
}

#content {
  border: 8px solid var(--white);
  border-top: 8px solid var(--text-purple);
  background-color: #0000;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  animation: 1s linear infinite spin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes spin {
  0% {
    transform: translate(-50%, -50%)rotate(0);
  }

  100% {
    transform: translate(-50%, -50%)rotate(360deg);
  }
}

.choices {
  z-index: 5;
  background-color: #fff3;
  border: none;
  border-radius: 20px;
  width: 280px;
  height: 40px;
  font-family: Montserrat, serif;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted, .choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
  background-color: #11010133;
}

.choices__inner {
  color: var(--text-gray);
  backdrop-filter: blur(5px);
  border: none;
  padding-left: 25px;
  font-size: 14px;
  font-weight: 400;
  line-height: 19.5px;
}

.choices[data-type*="select-one"] .choices__input {
  color: var(--text-gray);
  background-color: #11010133;
  border-radius: 0%;
}

.choices__list--dropdown, .choices__list[aria-expanded] {
  color: var(--text-gray);
  backdrop-filter: blur(5px);
  background-color: #11010133;
  border: none;
  font-weight: 700;
}

.choices__list--dropdown:hover {
  background-color: #11010133;
}

.choices__inner, .choices__list--dropdown, .is-active, .is-focused {
