  color: var(--white);
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  margin-top: -52px;
  padding: 20px;
  display: flex;
}

.event-container {
  box-sizing: border-box;
  text-align: center;
  background-color: #0000;
  border-radius: 10px;
  flex: 1 0 calc(21% - 13px);
  max-width: calc(60% - 30px);
  transition: transform .3s;
  position: relative;
  top: -94px;
}

.newContainer {
  color: #fff;
