  background-size: contain;
  width: 102px;
  height: 95px;
  position: absolute;
  top: -7px;
  left: 27px;
}

.img-event {
  width: 120px;
  height: 120px;
  position: inherit;
  object-fit: cover;
  z-index: 2;
  border-top-left-radius: 50px;
  border-bottom-right-radius: 50px;
  display: block;
}

.info-container {
  text-align: center;
  flex-direction: column;
  gap: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
}

.event-title {
  color: var(--text-purple);
  font-size: 14px;
  font-weight: 700;
  line-height: 17.07px;
}

.event-date {
  font-size: 14px;
  font-weight: 400;
  line-height: 17.07px;
}

.event-location {
  padding: 10px;
  font-size: 12px;
