  line-height: 14.63px;
}

.location-icon {
  background-color: #0000;
  background-image: url("location.8fc9d584.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 20px;
  height: 20px;
  margin-right: 5px;
}

#pagination-container {
  color: var(--white);
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
  display: flex;
}

#pagination-container #prev-btn, #next-btn {
  border: 1px solid var(--text-purple);
  color: var(--white);
  cursor: pointer;
  background-color: #0000;
  border-top-left-radius: 10px;
  border-bottom-right-radius: 10px;
  padding: 10px 15px;
  font-size: 16px;
  transition: transform .2s ease-in-out, border .2s ease-in-out;
}

#prev-btn:hover, #next-btn:hover {
  box-shadow: 3px 3px 5px 0px var(--text-purple);
  transform: translateY(-2px);
}

#pagination-container #prev-btn:disabled, #next-btn:disabled {
  border: 1px solid var(--white);
  cursor: not-allowed;
  box-shadow: none;
  transform: none;
}

.num-pagination {
  cursor: pointer;
  color: var(--white);
  background-color: #0000;
  border: none;
  padding: 10px;
  transition: transform .3s;
}

.num-pagination:hover {
  color: var(--text-purple);
  border-top-left-radius: 10px;
  border-bottom-right-radius: 10px;
  transform: translateY(-2px);
}

.footer-container {
  background-color: var(--background);
  justify-content: center;
  padding-top: 50px;
  padding-bottom: 50px;
  display: flex;
}

.copyright-container {
  color: #939393;
  text-align: center;
  width: 85%;
  font-size: 14px;
}

.modal {
  z-index: 5;
  backdrop-filter: blur(5px);
  background: #363636cc;
  width: 100%;
  height: 100%;
  animation: .3s forwards fadeIn;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    transform: translateY(-50px);
  }

  to {
    transform: translateY(0);
  }
}

.modal-content {
